/**
 * 호전환(즉시)
 * @param {String} tnt_id
 * @param {String} call_id
 * @param {String} conn_id
 * @param {String} dnis
 * @param {String} user_ani
 * @param {String} uui
 * @param {String} uei
 * @param {Object} route_option: {
      type: {int}                  // 0:일반, 1:지정, 2:직전, 3:전담
      priority: {int}              // 콜 우선순위
      relation_method: {int}       // 지정/직전/전담 상담사 옵션 (0 : 대기, 1 : 예약 2 : 우선)
      relation_agent_id: {string}  // 지정상담사 ID
      relation_timeout: {int}      // 지정 상담원 연결 타임아웃 시간
      method: {int}                // 라우팅 방법 (0 : Skill, 1 : Group, 2 : GroupSkill)
      skill_id: {string}           // 사용자 요구 스킬 ID
      skill_level: {int}           // 사용자 요구 스킬 레벨
      group_id: {string}           // 사용자 요구 GROUP ID
    }
 * @returns axios
 */
export function singleStepTransfer(tnt_id, call_id, conn_id, dnis, user_ani, uui, uei, route_option) {
  const params = {
    tenant: tnt_id,
    call_id: call_id,
    conn_id: conn_id,
    dnis: dnis,
    user_ani: user_ani,
    uei: uei,
    uui: uui,
  };

  params.route_opt = {
    type: route_option.type,
    priority: route_option.priority,
    relation_method: route_option.relation_method,
    relation_agent_id: route_option.relation_agent_id,
    relation_timeout: route_option.relation_timeout,
    method: route_option.method,
    skill_id: route_option.skill_id,
    skill_level: route_option.skill_level,
    group_id: route_option.group_id,
  };

  return apiClient().axios.post(`${API_NAME}/singlestep-transfer/${tnt_id}`, params);
}

